.widget_shopping_cart_content {

  .cart_list {
    overflow: hidden;
    overflow-y: auto;
    padding-right: 18px;
    margin-right: -18px;
    padding-left: 0;
    margin-left: 0;
  }

  .mini_cart_item {
    border-bottom: 1px solid $color-accent;
    padding: 8px 0;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      border-bottom: none;
    }

    img {
      aspect-ratio: 4/5.3;
      width: 100%;
      object-fit: cover;
    }

    .product-image {
      margin: 0 16px 0 0;
      max-width: 80px;
    }

    .product-details {
      padding-right: 37px;
      line-height: 24px;
      font-size: 15px;
    }

    .remove_from_cart_button {
      margin-top: -12px;
      color: $body-color;

      &:hover {
        color: $danger-color;
      }

      i {
        font-size: 20px;
      }
    }

    .quantity {
      display: inline-block;

      .box {
        border-radius: 8px;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        -ms-border-radius: 8px;
        -o-border-radius: 8px;
      }
    }
  }

  .amount {
    color: $primary-color;
    font-weight: 500;
    font-size: 15px;
    margin-left: 5px;
  }

  dl.variation {
    dt {
      font-size: 14px;
    }

    dd {
      color: $body-color;
      font-size: 15px;
    }
  }

  /* Footer Cart */
  .woocommerce-mini-cart__total {
    border-top: 1px solid $color-accent;

    &>strong {
      font-size: 17px;
      font-weight: 500;
    }

    &>span {
      font-size: 20px;
    }
  }

  .woocommerce-mini-cart__buttons {
    a:first-child {
      background-color: $dark-color;

      &:hover {
        background-color: $primary-color;
      }
    }
  }

  /* Cart empty */
  .inner-empty {
    .empty-heading {
      font-size: 14px;
      line-height: 20px;
      color: #6b6b6b;
      margin-bottom: 5px;
    }

    .wc-continue {
      color: $primary-color;
      background: 0 0;
      font-size: 16px;
      line-height: 22px;
      padding: 0;
      margin-top: 10px;
      font-weight: 600;
    }

    i {
      font-size: 10px !important;
      margin-left: 5px;
      line-height: 1;
      display: inline-block;
      vertical-align: middle;
    }
  }
}

/* Freeshipping conditional */ 
.total-condition-wrap {
  .total-condition {
    width: 100%;
    position: relative;
    background-color: rgba(0, 0, 0, .1);
    height: 16px;
    line-height: 16px;
    font-size: 12px;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;

    .total-condition-hint {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 0;
      line-height: inherit;
      color: transparent;
      text-align: center;
    }

    .subtotal-condition {
      color: $light-color;
      background: $bg-free-shipping;
      position: relative;
      z-index: 1;
      line-height: inherit;
      width: 2px;
      height: 100%;
      overflow: hidden;
      text-align: center;
      font-weight: 400;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
    }

    .total-condition-hint,
    .subtotal-condition {
      -webkit-transition: width .5s ease;
      -o-transition: width .5s ease;
      transition: width .5s ease;
    }
  }

  .total-condition-desc {
    max-width: 620px;
    margin: 3px auto 13px;
    text-align: center;
    line-height: 27px;
    font-size: 15px;
    font-weight: 400;
    color: #6b6b6b;

    .amount {
      color: $primary-color;
      font-size: 18px;
      line-height: 31px;
      font-weight: 400;
    }

    strong {
      font-weight: 400;
    }
  }

  .hide-in-cart {
    display: none;
  }
}

/* Cart */ 
.wc-proceed-to-checkout {
  .button {
    width: 100%;
  }
}

.cart-total-info {
  margin-top: 60px;
}

.cart_totals {
  width: 100% !important;

  h2 {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
    margin: 0;
    border-bottom: 1px solid #f1f1f1;
    padding-bottom: 21px;
    text-transform: capitalize;
    color: $color-text;
  }

  .wc-proceed-to-checkout {
    display: none;
  }
}