.header-logo {
  padding-top: 18px;

  img {
    width: 140px;
  }
}

.nav-right {
  min-width: 250px;

  &>div {
    flex-wrap: wrap;
  }

  .header-icon {
    &.header-account {
      margin-top: -2px;
    }

    &>a {
      height: 40px;
      min-width: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 40px;
      position: relative;
    }

    a {
      i {
        font-size: 20px;
      }
    }

    .recently-viewed-icon {
      margin-bottom: -3px;
    }
  }

  .divider {
    width: 1px;
    height: 20px;
    background-color: #ded6d1;
    margin: 0 24px 0 16px;
  }

  .text-cart {
    text-align: left;
    margin-left: 14px;
    margin-right: 0;
    color: $color-text;
    line-height: 22px;
    -webkit-transition: .3s all;
    -o-transition: .3s all;
    transition: .3s all;
    font-size: 14px;
  }

  .dropdown-toggle {
    &::after {
      display: none;
    }
  }

  .dropdown-menu {
    width: 390px;
    margin: 0;
    padding: 16px;
    inset: 100% 1px auto auto !important;
    border: 1px solid $color-accent;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
    transform: translate(0, 31px) !important;
    background-clip: unset;
  }
}

.header {
  background-color: #fafafa;

  &.sticky {
    .nav-top {
      display: none;
    }

    .main-nav {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 999;
      left: 0;
      right: 0;
      border-bottom: 1px solid #f2f2f2;
      background-color: #fafafa;
    }
  }
}

.admin-bar {
  header.sticky {
    margin-top: 90px;

    .main-nav {
      top: 32px;
    }
  }
}

/* Mobile header nav */
.mobile-header-nav {
  transition: all .4s ease-in-out 0s;
  z-index: 99;

  .navbar-device-mobile {
    height: 48px;
    padding: 0 15px;

    i {
      font-size: 24px;
    }
  }

  .bottombar-device-mobile {
    padding: 0 15px;
  }

  .custom-logo-link {
    img {
      padding-left: 5px;
      width: 100px;
      height: auto;
    }

    span {
      letter-spacing: 2px;
      font-size: 16px;
      padding-left: 5px;
      font-weight: 600;
    }
  }

  .mini-cart {
    margin-right: 5px;
  }
}

/* Mobile footer nav */
.mobile-footer-nav {
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, .1);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, .1);
  bottom: -1px;
  z-index: 99;

  .list-menu-icon>* {
    line-height: 1.5;
  }

  a.active {
    &::before {
      content: '';
      position: absolute;
      top: -1px;
      left: 0;
      right: 0;
      width: 100%;
      height: 3px;
      background-color: $primary-color;
      color: #fff;
    }

    span,
    i {
      color: $color-text;
    }
  }

  .menu-icon-child {
    padding: 9px 0 5px;

    i {
      font-size: 20px;
      line-height: 1;
      color: #6b6b6b;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      -webkit-box-flex: 1;
      -moz-box-flex: 1;
      -webkit-flex: 1;
      -ms-flex: 1;
      flex: 1;
      align-items: center;
      justify-content: center;
    }

    span {
      font-size: 13px;
    }
  }
}

.mobile-quickbuy-nav {
  bottom: -1px;
  z-index: 999;
  height: 58px;

  .row {
    padding: 0 5px;
    --bs-gutter-x: 10px;
    --bs-gutter-x-inverse: -10px;
  }

  .button {
    height: 40px;
    line-height: 40px;
  }
}